<template>
  <div class="user">
    <div class="top">
      <header>
        <Icon type="person-stalker" size="23"></Icon>
        <span>用户管理</span>
      </header>
      <p></p>
    </div>
    <div class="search">
      <div class="item">
        <span style="margin-bottom: 0.1rem;display: inline-block">用户昵称</span>
        <Input placeholder="请输入用户昵称" v-model.trim="name" style="width: 2rem"></Input>
      </div>
      <div class="item item_last">
        <a href="javascript:void(0)" @click="search" class="search_button">搜索</a>
      </div>
    </div>
    <div>
      <div style="width: calc(100vw - 3.5rem);margin-top: 0.2rem">
        <!--<Table  :columns="columns" :data="data" :no-data-text="msg"></Table>-->
         <div style="width: calc(100vw - 3.5rem);margin-top: 0.2rem;box-shadow: 5px 5px 10px #E7E7E7;">
        <table>
        <tr>
          <th>昵称</th>
          <th>头像</th>
          <th>性别</th>
          <th>省市区</th>
          <th>加入时间</th>
        </tr>
        <tr class="td" v-for="(item,key) in data">
          <td>{{item.nickName}}</td>
          <td><img :src="item.avatarUrl" width="20px" height="20px"/></td>
          <td>{{item.gender}}</td>
          <td>{{item.province}}</td>
          <td>{{item.create_time}}</td>
        </tr>
      </table>
      <p v-if="data.length===0" class="td_data">
        <img src="../../../assets/no-data.png" class="img_box"/>
      </p>
      </div>

      </div>
      <footer style="padding: 0.2rem 0;box-shadow: 5px 5px 10px #E7E7E7;background: white;width: 96%;padding-right: 0.2rem;">
        <Page :total="total" size="small" show-total show-elevator show-sizer @on-change="changePage" @on-page-size-change="changeSize"></Page>
      </footer>
    </div>
  </div>
</template>
<script>
export default{
    data(){
        return {
          msg:'数据加载中....',
          name:'',
          columns:[
              {
            title:"昵称",
            key: 'nickName'
          },{
              title: '头像',
              key: 'action',
              render: (h, params) => {
                return h("img",{
                      attrs:{
                          src:params.row.avatarUrl
                      },
                    style:{
                          width:"20px",
                          height:"20px"
                    }
                  })
              }
            },{
              title: '性别',
              key: 'gender'
            },{
              title: '省市区',
              key: 'city',
              render: (h, params) => {
                return h('div', [
                  h('strong', params.row.country),
                  h('strong', params.row.province)
                ]);
              }
            },{
              title:"加入时间",
              key:"create_time"
            }
            ],
          data:[],
          total:0,
        }
    },
  methods:{
//获取用户表
    getUserList(page,num){
        this.post("custom/Own/getUserList",{
          nickname:this.name,
          page:page,
          limit_num:num
        }).then(res=>{
            if(res.data.code===10000){
              this.data=res.data.data;
              this.total = res.data.number;
              this.data.forEach(item=>{
                if(item.gender == 1){
                  item.gender = "男"
                }else{
                  item.gender = "女"
                }
                item.create_time=new Date(item.create_time*1000).toLocaleString();
              });              
              if(this.data.length===0){
               this.msg="暂无数据"
              }
            }else{
            	 const content = res.data.msg_test
			          this.$Modal.error({
			                title: "获取失败",
			                content: content
			           });
            }
        })
    },
    changePage(val){
      this.getUserList(val);
    },
    changeSize(val){
      this.getUserList(1,val);
    },
    search(){
        this.getUserList(1,10);
    }
  },
  mounted(){
    this.$store.commit('setStatus',false);
    this.getUserList();
  }
}
</script>
<style scoped>
  @import '../../../../static/base.css';
  @import url("../../../../static/table.css");
</style>



